.admin-dashboard {
  margin: 50px auto;
  width: 60%;
  text-align: center;
  background-color: #f8f9fa;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

.admin-dashboard h2 {
  margin-bottom: 20px;
  color: #333;
}

.upload-section {
  margin: 20px 0;
}

.upload-section input[type="file"] {
  margin-right: 10px;
}

.upload-section button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.upload-section button:hover {
  background-color: #0056b3;
}

.upload-status {
  margin-top: 20px;
  color: green;
  font-weight: bold;
}
